body {
  font-family: 'Space Mono', monospace, 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-image: url("./background.png");
  background-size: cover;
  background-color: black;

  display: grid;
  grid-template-rows: min-content auto auto auto auto auto auto auto min-content;

  width: 100vw;
  min-width: 100vw;
  max-width: 100vw;
  margin: 0;

  overflow-x: hidden;
  overflow-y: auto;
}
